/* === CartIQ Navbar: Coffee Cream Theme â˜• === */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 250, 245, 0.95); /* Creamy coffee bg */
  padding: 1rem 2rem;
  border-bottom: 1px solid #e0c5aa;
  box-shadow: 0 4px 12px rgba(68, 44, 26, 0.08);
  backdrop-filter: blur(6px);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.nav-logo {
  font-size: 1.8rem;
  font-weight: 900;
  color: #5e4033;
  font-family: "Segoe UI", sans-serif;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* === Nav Links === */
.nav-links {
  list-style: none;
  display: flex;
  gap: 1.4rem;
  align-items: center;
}

.nav-links li a {
  text-decoration: none;
  color: #4e342e; /* Dark coffee text */
  font-weight: 600;
  padding: 8px 16px;
  border-radius: 10px;
  transition: all 0.3s ease;
  font-size: 1rem;
  font-family: "Segoe UI", sans-serif;
}

.nav-links li a:hover,
.nav-links li a.active {
  background: linear-gradient(135deg, #b67d5c, #836953); /* mocha gradient */
  color: #fffaf7;
  box-shadow: 0 4px 12px rgba(96, 66, 50, 0.15);
  transform: translateY(-2px);
}

/* === Mobile Responsive === */
@media screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem 1.5rem;
  }

  .nav-links {
    flex-direction: column;
    width: 100%;
    gap: 0.8rem;
    margin-top: 1rem;
  }

  .nav-links li {
    width: 100%;
  }

  .nav-links li a {
    width: 100%;
    display: block;
    padding: 10px 14px;
    background-color: #f8f1eb;
    border-radius: 8px;
    transition: all 0.2s ease-in-out;
  }

  .nav-links li a:hover,
  .nav-links li a.active {
    background: linear-gradient(135deg, #836953, #b67d5c);
    color: #ffffff;
  }
}
